* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* Fonts */
/* font-family: 'Bangers', cursive;

font-family: 'Karla', sans-serif;

font-family: 'Poppins', sans-serif; */

.nav-div {
  background-color: #cbf381fd;
  padding-top: 20px;
}
.nav--bar {
  background-color: rgb(61, 21, 95);
  display: flex;
  justify-content: space-around;
  margin: 0 20px 0 20px;
  padding: 20px;
  box-shadow: 0px 3.98256px 7.4564px #cbf381fd;
  border-radius: 30px 30px;
}
.logo--text {
  color: #ccf381;
  font-family: "Bangers", cursive;
}
.nav--list {
  font-family: "Poppins", sans-serif;
  list-style: none;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.nav--list li {
  padding: 5px;
  color: #ccf381;
  margin-left: 20px;
  cursor: pointer;
}
.nav--list .ct-li:hover {
  text-decoration: underline;
  transform: scale(1.1);
  transition: 0.2s;
  text-decoration-thickness: 3px;
  text-underline-offset: 8px;
  color: #ccf381;
}
/* Top section */
.top-main {
  padding: 100px 0 100px 0;
  background-color: #ccf381;
  text-align: center;
}
.top-header1 {
  font-family: "Poppins", sans-serif;
  font-size: 50px;
  cursor: pointer;
}
.span-1,
.span-2 {
  font-family: "Bangers", cursive;
  color: rgb(61, 21, 95);
  cursor: pointer;
}

.top-header2 {
  font-family: "Poppins", sans-serif;
  cursor: pointer;
}

/* Get Quote Section */
.qt-text {
  font-size: 30px;
  text-align: center;
  font-family: "Poppins", sans-serif;
}
.form-main {
  text-align: center;
  margin-bottom: 200px;
}
.btn--1 {
  width: 80%;
  margin: 20px 0;
  padding: 10px 20px;
  font-family: "Poppins", sans-serif;
  font-size: 32px;
  font-weight: 400;
  color: white;
  background: linear-gradient(
    90deg,
    rgb(61, 21, 95) 1.18%,
    rgba(61, 21, 95, 0.781) 100%
  );
  border: none;
  border-radius: 8px;
}
.btn--1:hover {
  transform: scale(1.1);
  transition: 0.4s;
}
/* Footer */
.footer {
  padding: 50px 0;
  background-color: rgb(61, 21, 95);
  text-align: center;
}
.footer-text1,
.footer-text2 {
  font-family: "Poppins", sans-serif;
  color: #ccf381;
}
.span-3 {
  font-family: "Bangers", cursive;
  cursor: pointer;
}
.footer-text2 a {
  color: #ccf381;
  text-decoration: none;
}
